import Vue from 'vue'
import Router from 'vue-router'
import Blogs from "../components/Blogs";
import BlogEdit from "../components/BlogEdit";
import BlogDetail from "../components/BlogDetail";
import Login from "../components/Login";
import UserAdd from "../components/UserAdd";

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {path:'/',name:'Index',redirect:{name:"Blogs"}},
    {path:'/blogs',name:'Blogs',component:Blogs},
    {path:'/login',name:'Login',component:Login},
    {path:'/blog/add',name:'BlogAdd',component:BlogEdit, meta: {requireAuth: true}},
    {path:'/blogDetail/:blogId',name:'BlogDetail',component:BlogDetail},
    {path:'/blogEdit/:blogId',name:'BlogEdit',component:BlogEdit, meta: {requireAuth: true}},
    {path:'/userAdd',name:'UserAdd',component:UserAdd}
  ]
})







