<template>
<div>
  <Header></Header>
  <div class="block">
    <div id="button" v-if="userID||userID===0">
      <el-radio v-model="radio" label="1">浏览全部</el-radio>
      <el-radio v-model="radio" label="2">只看自己</el-radio>
    </div>
    <el-timeline>
      <el-timeline-item   :timestamp="blog.created" placement="top" v-for="blog in blogs" :key="blog.id">
        <el-card>
          <h3 >
            <router-link  class="font" :to="{name:'BlogDetail',params:{blogId: blog.id}}">{{ blog.title }}</router-link>
          </h3>
          <p><i class="el-icon-user-solid">:{{blog.userName}}</i></p>
        </el-card>
      </el-timeline-item>
    </el-timeline>
    <el-pagination class="mpage"
        background
        layout="prev, pager, next"
        :current-page="currentPage"
        :page-size="pageSize"
        :total="total"
        @current-change=page
      >
    </el-pagination>
  </div>
</div>
</template>

<script>
import Header from "./Header";
export default {
  name: "Blogs",
  data() {
    return {
      blogs: {},
      currentPage: 1,
      total: 0,
      pageSize: 5,
      radio: '1',
      userID:''
    }
  },
  components: {Header},
  methods: {
    page(currentPage) {
      if (this.radio==1){
        this.$http.get("/blogs?currentPage=" + currentPage).then(res => {
          console.log(res);
          this.blogs = res.data.data.records;
          this.currentPage = res.data.data.current;
          this.total = res.data.data.total;
          this.pageSize = res.data.data.size;
        })
      }else{
        this.$http.get("/blogSelf?currentPage=" +currentPage+"&userID="+this.userID).then(res => {
          console.log(res);
          this.blogs = res.data.data.records;
          this.currentPage = res.data.data.current;
          this.total = res.data.data.total;
          this.pageSize = res.data.data.size;
        })
      }
    },
    all(){
      this.page(1)
    },
    self(){
      this.$http.get("/blogSelf?currentPage=" +1+"&userID="+this.userID).then(res => {
        console.log(res);
        this.blogs = res.data.data.records;
        this.currentPage = res.data.data.current;
        this.total = res.data.data.total;
        this.pageSize = res.data.data.size;
      })
    }
  },
  created() {
    this.page(1)
    this.userID=this.$store.getters.getUser.id
    console.log(this.userID)
  },
  watch:{
    'radio':{
      handler:function (){
        if(this.radio==1){
          console.log(1)
          this.page(1)
        }else{
          console.log(2)
          this.self()
        }
      },
      deep:true
    }
  }
}
</script>

<style scoped>
  .mpage{
    text-align: center;
    margin: 0 auto;
  }
  .block{
    max-width: 60%;
    margin: 0 auto;
    margin-bottom: 50px;
  }
  .font{
    text-decoration: none;
    color: dodgerblue;
  }
  #button{
    text-align: right;
  }
</style>



