<template>
  <div>

    <Header></Header>
    <div class="mblog">
      <el-backtop>
        <div
          style="{
                height: 100%;
                width: 100%;
                background-color: #f2f5f6;
                box-shadow: 0 0 6px rgba(0,0,0,.12);
                text-align: center;
                line-height: 50px;
                color: #1989fa;
               }"
        >UP</div>
      </el-backtop>

      <h2 class="font">{{ruleForm.title}}</h2>
      <span v-if="ownBlog||this.$store.getters.getUser!=null&&this.$store.getters.getUser.id===0" class="but">
        <router-link :to="{name:'BlogEdit',params:{blogId:ruleForm.id}}">
          <el-link icon="el-icon-edit">
            编辑
          </el-link>
        </router-link>

        <el-link @click="remove(ruleForm.id)">
          <i class="el-icon-delete"></i>删除
        </el-link>

      </span>



      <el-divider></el-divider>
      <div class="markdown-body">
        <vue-markdown  :source="ruleForm.content"></vue-markdown>
      </div>
      <div class="markdown-body"  v-html="ruleForm.content" >

      </div>
    </div>
    <div id="bu">
      <el-button @click="out" >退出</el-button>
    </div>
  </div>
</template>


<script>
import Header from "./Header";
import "github-markdown-css";
export default {
  name: "BlogDetail.vue",
  data(){
    return{
      ruleForm:{
        id:"",
        title:"",
        description:"",
        content:"",
        userID:""
      },
      username:"",
      ownBlog:false
    }
  },
  methods:{
    remove(id){
      this.$http.get("/deleteBlog/"+id,{
        headers: {
          "Authorization": localStorage.getItem("token")
        }
      }).then(res=>{
        this.$alert('删除成功', '提示', {
          confirmButtonText: '确定',
          callback: action => {
            this.$router.push("/blogs")
          }
        });
      })
    },
    out(){
      this.$router.push("/blogs")
    }
  },
  components: {Header},
  created() {
    const blogId=this.$route.params.blogId
    if(blogId){
      this.$http.get("/blog/"+blogId).then(res=>{

        const blog=res.data.data;
        console.log(blog);
        this.blog=blog;
        this.ruleForm.id=blog.id;
        this.ruleForm.title=blog.title;
        this.ruleForm.description=blog.description;
        this.ruleForm.userName=blog.userName;


        var MarkdownIt = require('markdown-it');
        var md = new MarkdownIt();
        var result = md.render(blog.content);
        this.ruleForm.content=result;
        console.log(this.$store.getters.getUser)
        this.ownBlog=(this.$store.getters.getUser!=null&&blog.userId===this.$store.getters.getUser.id)

      })

    }
  }
}
</script>

<style scoped>
  .mblog{
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    width: 70%;
    margin: 20px auto;
    min-height: 700px;
    padding: 20px;
  }
  .but{
    float: right;
  }
  .font{
    color: dodgerblue;
  }
  #bu{
    margin: 0 auto;
    width: 100%;
    text-align: center;
    margin-bottom: 50px;
  }
</style>



