<template>
  <div class="m-content">
    <h3>欢迎来到nate_18163126的博客</h3>
    <div class="block">
      <el-avatar :size="50" :src="user.avatar"></el-avatar>
      <div>{{user.username}}</div>
    </div>
    <div class="maction">
      <span><el-link href="/blogs">主页</el-link></span>

      <el-divider direction="vertical"></el-divider>

      <span><el-link href="/blog/add" type="success">发表博客</el-link></span>

      <el-divider direction="vertical"></el-divider>

      <span v-if="!hasLogin"><el-link href="/login" type="primary">登陆</el-link></span>

      <span v-if="hasLogin"><el-link @click="logout" type="danger">退出</el-link></span>
    </div>
  </div>
</template>

<script>
export default {
  name: "Header",
  data(){
    return{
      user:{
        username:"请先登陆",
        avatar:"https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
      },
      hasLogin:false
    }
  },
  methods:{
    logout(){
      this.$http.get("/logout",{
        headers:{
          "Authorization":localStorage.getItem("token")
        }
      }).then(res=>{
        this.$store.commit("REMOVE_INFO")
        this.$router.push("/login")
      })
    }
  },
  created() {
    if (this.$store.getters.getUser.username){
      this.user.username=this.$store.getters.getUser.username;
      this.user.avatar=this.$store.getters.getUser.avatar;
      this.hasLogin=true;
    }else{
      this.hasLogin=false;
    }
  }
}
</script>

<style scoped>
  .m-content{
    max-width: 40%;
    margin: 0 auto;
    text-align: center;
  }
  .maction{
    margin: 10px 0;
  }
</style>



