<template>
  <div class="add">

    <el-container>
      <el-header>
        <el-header>
        <img class="logo" src="https://www.markerhub.com/dist/images/logo/markerhub-logo.png">
        </el-header>
      </el-header>
      <el-main>
        <el-form   :model="ruleForm"  :rules="rules" ref="ruleForm" label-width="100px" class="add_width">
          <el-form-item  label="头像"    required >
            <el-select v-model="ruleForm.avatar" placeholder="请选择" class="photo">
              <el-option
                v-for="item in options"
                :key="item.avatar"
                :label="item.label"
                :value="item.avatar">
              </el-option>
            </el-select>
            <el-avatar :size="50" :src="ruleForm.avatar"></el-avatar>

          </el-form-item>
          <el-form-item label="用户名" prop="username" required>
            <el-input v-model="ruleForm.username"  ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password" required>
            <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass" required>
            <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item prop="email" label="邮箱" required>
            <el-input v-model="ruleForm.email"></el-input>
          </el-form-item>
          <el-form-item label="生日" required>
            <el-col >
              <el-form-item prop="created">
                <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.created" style="width: 100%;"></el-date-picker>
              </el-form-item>
            </el-col>
          </el-form-item>
          <el-form-item class="button">
            <el-button type="primary" @click="submitForm('ruleForm')">立即注册</el-button>
            <el-button @click="toLogin">退出</el-button>
          </el-form-item>
        </el-form>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "UserAdd",
  data() {
    var validateName=(rule, value, callback)=>{
      this.$http.get("/user/name/"+value).then(res=>{
        if(res.data.data!=null){
          callback(new Error("用户名已注册"))
        }else{
          callback();
        }
      })

    }

    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.ruleForm.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      options: [{
        avatar: 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=278856886,2004647430&fm=26&gp=0.jpg',
        label: '亚索'
      }, {
        avatar: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=226155697,3281875826&fm=26&gp=0.jpg',
        label: '盖伦'
      }, {
        avatar: 'https://ss1.baidu.com/6ON1bjeh1BF3odCf/it/u=2707198734,661233760&fm=15&gp=0.jpg',
        label: '金克斯'
      }, {
        avatar: 'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=2722445516,1571741308&fm=26&gp=0.jpg',
        label: 'vn'
      }, {
        avatar: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=2400920312,4079163394&fm=26&gp=0.jpg',
        label: '艾希'
      },{
        avatar: 'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=4214578008,3346099245&fm=15&gp=0.jpg',
        label: '妖姬'
      },{
        avatar: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1608046327,3421192192&fm=26&gp=0.jpg',
        label: '莫甘娜'
      }],
      avatar: 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=278856886,2004647430&fm=26&gp=0.jpg',

      ruleForm: {
        username: '',
        password:'',
        created: '',
        checkPass:'',
        avatar:'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=278856886,2004647430&fm=26&gp=0.jpg'
      },
      rules: {
        username: [
          { required: true, message: '请输入账号', trigger: 'blur' },
          { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' },
          { validator: validateName, message: "用户名已注册"}
        ],
        created: [
          { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
        ],
        qwe: [
          { required: true, message: '请选择头像',trigger: 'change'  }
        ],
        password: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        email:[
          { required: true, message: '请输入邮箱地址', trigger: 'blur' },
          { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.ruleForm);
          this.$http.post("user/save",this.ruleForm).then(res=>{
            console.log(res);
            this.$alert('注册成功', '提示', {
              confirmButtonText: '确定',
              callback: action => {
                this.$router.push("/login")
              }
            });
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    toLogin(){
      this.$router.push("/login");
    }
  }
}
</script>

<style scoped>
  .add{
    width: 100%;
    margin: 0 auto;
  }
  .logo{
    height: 60%;
    margin-top: 10px;
  }
  .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }

  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }

  .el-main {
    color: #333;
    text-align: center;
    line-height: 160px;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }
  .demo-ruleForm{
    margin: 0 auto;
  }
  .button{
    margin-right: 30px;
  }
  .add_width{
    width: 30%;
    margin: 0 auto;
  }
  .photo{
    float: left;
  }
</style>



